@import '../../styles/colors';
@import '../../styles/shared';

.home {
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  font-size: calc(10px + 2vmin);
  color: $global-text;
  position: relative;
  overflow: hidden;

  &__logo {
    height: calc(150px + 5vmin);
    border-radius: 100%;
    filter: contrast(1.25) brightness(0.75);
  }

  &__buttons {
    display: flex;
    column-gap: 1.5em;

    font-size: calc(10px + 1vmin);
  }

  &__button {
    @include link-shared;
    $trans-dur: 0.2s;
    $trans-back-dur: 0.4s;

    color: $global-text;
    font-weight: 500;
    letter-spacing: 0.08em;

    padding: 0.5em 1em;

    display: inline-flex;
    align-items: center;
    column-gap: 0.3em;

    background-color: #000;
    border-radius: 0.3em;

    position: relative;

    overflow: hidden;

    transition-duration: 0.5s;
    transition-property: color, box-shadow, transform;
    transition-delay: $trans-back-dur, 0s;

    @extend %default-link-effects;

    & * {
      z-index: 1;
    }

    &::before {
      content: '';
      position: absolute;

      bottom: 0;
      left: 0;

      width: 100%;
      height: 1px;

      background-color: $link-color;

      transition-duration: $trans-dur;
      transition-delay: $trans-back-dur;
    }

    &:hover {
      transition-delay: $trans-dur - 0.02s;
      box-shadow: 0 0 10px $link-color, 0 0 20px $link-color, 0 0 40px $link-color,
        0 0 80px $link-color, 0 0 160px $link-color;

      @media screen and (hover: hover) {
        color: #000;
      }

      &::before {
        transition-delay: 0s;
        @media (hover: hover) {
          height: 100%;
        }
      }
    }

    &-icon {
      height: 1.5em;
      width: 1.5em;

      display: inline-flex;
      justify-content: center;
      align-items: center;
    }

    &-text {
      margin: 0;
    }
  }
}
